body, html {
    background-color: black;
    overflow: hidden;
}
button {
    color: white;
}

.app_container {
    display: flex;
    color: white;
    height: 100vh;
    /* flex-direction: row-reverse; */
    overflow: hidden;
}

.mobile_hero_container {
    display: none;
}

.Right_container {
    width: 80%;
    background-image: url(image/background.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

}

/* .Right_top {
    height: 30vh;
    border: 2px solid red;
} */

.Right_bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    bottom: 4rem;
    right: 0;
    left: 0;
    /* height: 60%; */
    padding: 0px 1rem;
    padding-bottom: 2rem;
}

.question {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid white;
    background-image: linear-gradient(rgb(27, 27, 155), rgb(13, 13, 56));
    border-radius: .43rem;
    width: 80%;
    margin-bottom: 3rem ;
    padding: 2rem 0;
    text-align: center;
    z-index: 9999999999999999;



    
}

.question p {
    margin: 0;
    padding: .754rem 0;
    font-size: 1.25rem;
}

/* .answer_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
} */

.answer_container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    gap: 1rem;
}




.answer_container:hover .answer {
    /* background-color: rgb(0, 89, 255); */
    cursor: pointer;
    
}

.answer {
    border:  2px solid white;
    width: 26vw;
    min-width: 14rem;
    border-radius: 10px;
    font-size: 1.15rem;
    padding: .52rem 0rem;
    background-image: linear-gradient(rgb(27, 27, 155), rgb(13, 13, 56));


}

.answer:hover {
    background-image: linear-gradient(rgb(0, 81, 255), rgb(0, 4, 255));
    cursor: pointer;
    
}

.answer.active {
    background-image: linear-gradient(rgb(0, 81, 255), rgb(0, 4, 255));
    
}

.answer.correct {
    animation: correct 3s ease forwards;
}

.answer.incorrect {
    animation: incorrect 3s ease forwards;
}

@keyframes correct {
    0%, 22%, 42% , 62%, 99%{
        background-image: linear-gradient(rgb(27, 27, 155), rgb(13, 13, 56));
    }

    20%, 40%, 60% , 80%{
    background-image: linear-gradient(rgb(0, 81, 255), rgb(0, 4, 255));
        
    }
      100% {
        background-image: linear-gradient(green, rgb(2, 201, 68), green);
    }

}

@keyframes incorrect {
    0%, 22%, 42% , 62%, 99%{
        background-image: linear-gradient(rgb(27, 27, 155), rgb(13, 13, 56));
    }

    20%, 40%, 60% , 80%{
    background-image: linear-gradient(rgb(0, 81, 255), rgb(0, 4, 255));
        
    }
      100% {
        background-image: linear-gradient(red, rgb(167, 12, 12));
    }

}


.counter ,
.mobile_counter {
    text-align: start;
    width: 80%;
    margin-bottom: 2rem;

}
.counter p ,
.mobile_counter p{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: start;
    border: 2px solid white;
    width: 2.963rem;
    height: 2.963rem;
    border-radius: 100px;
    background-image: linear-gradient(rgb(27, 27, 155), rgb(13, 13, 56));

}

.left-continer {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 20%;
    padding: 0px 1rem;
    background-image: linear-gradient(rgb(27, 27, 155), rgb(13, 13, 56));
    padding-bottom: 2rem ;
    padding-top: 1rem;

    
}
 
.single_step {
   display: flex;
   width: 100%;
}

.single_step.active {
    border: 2px solid white;
    background-image: linear-gradient(rgb(0, 81, 255), rgb(0, 4, 255));
    border-radius: 10px;
}
.single_step p {
    margin: 0rem;
    padding: .2rem 0;
    padding-left: .5rem ;
}

.money_id {
    width: 40%;
}







.left-continer p {
    font-size: 1.13rem;
}



.looser_container {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 6px solid red;
    background-color: rgb(124, 36, 36);
    width: 80%;
    padding: 5rem 0;
    z-index: 99999999999;
}

.win_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    border: 6px solid green;
    background-color: rgb(29, 97, 29);
    /* width: 80%; */
    width: 50vw;
    min-width: 20rem;
    padding: 5rem 0;
    z-index: 9999999999999999999999999999;
}

.play_again {
    background-color: green;
    color: black;
    font-weight: bold;
    padding: .8rem 2rem;
    border: none;
    border-radius: 10px;
    color: white;
    font-size: 1rem;
}



